// src/components/ScrollToTopButton.tsx
import React, { useState, useEffect } from 'react';

const ScrollToTopButton = () => {
    const [isVisible, setIsVisible] = useState(false);

    // Fonction pour grer le dfilement
    const handleScroll = () => {
        if (window.scrollY > 200) {
            setIsVisible(true); // Affiche le bouton lorsque le dfilement dpasse 200px
        } else {
            setIsVisible(false); // Cache le bouton si l'utilisateur est revenu en haut
        }
    };

    // couteur d'vnements pour le dfilement
    useEffect(() => {
        window.addEventListener('scroll', handleScroll); // Ajoute l'couteur d'vnement au scroll
        return () => {
            window.removeEventListener('scroll', handleScroll); // Nettoie l'couteur lorsqu'il n'est plus ncessaire
        };
    }, []);

    // Fonction pour faire dfiler la page vers le haut
    const scrollToTop = () => {
        window.scrollTo({ top: 0, behavior: 'smooth' });
    };

    // Si isVisible est false, ne rien rendre
    if (!isVisible) return null;

    return (
        <button
            onClick={scrollToTop}
            style={{
                position: 'fixed',
                bottom: '20px',
                right: '20px',
                backgroundColor: 'orange', // Orange fonc
                color: 'white',
                width: '50px', // Largeur carre
                height: '50px', // Hauteur carre
                borderRadius: '8px', // Coins lgrement arrondis pour un effet carr avec des bords
                border: 'none',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                cursor: 'pointer',
                fontSize: '24px',
                boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)', // Lgre ombre porte
                zIndex: 1000, // S'assurer qu'il est au-dessus des autres lments
                transition: 'opacity 0.3s ease-in-out', // Animation douce d'apparition
            }}
        >
            <i className="bi bi-arrow-up-short"></i> {/* Icne flche vers le haut */}
        </button>
    );
};

export default ScrollToTopButton;
