// export default About;
import React, { useState, useEffect } from 'react';
import AOS from 'aos';
import 'aos/dist/aos.css'; // Importer les styles CSS d'AOS
import TestimonialsSimpl from './TestimonialsSimpl';
import TeamSimpl from './TeamSimpl';
import bsl3 from '../../assets/img/bsl3.jpg'; // Assurez-vous que le chemin est correct

const About: React.FC = () => {
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        // Initialiser AOS (Animate On Scroll)
        AOS.init();

        const handleLoad = () => setLoading(false);

        const images = [bsl3];
        let loadedImages = 0;

        images.forEach((src) => {
            const img = new Image();
            img.src = src;
            img.onload = () => {
                loadedImages += 1;
                if (loadedImages === images.length) {
                    handleLoad();
                }
            };
        });

        return () => {
            images.forEach((src) => {
                const img = new Image();
                img.src = src;
                img.onload = null;
            });
        };
    }, []);

    return (
        <main className="main">
            {loading ? (
                <div className="loading-indicator">Chargement...</div>
            ) : (
                <>
                    {/* Page Title */}
                    <div className="about-page"></div>
                    <div className="page-title accent-background">
                        <div className="container d-lg-flex justify-content-between align-items-center">
                            <h1 className="mb-2 mb-lg-0"> propos</h1>
                            <nav className="breadcrumbs">
                                <ol>
                                    <li><a href="/">Accueil</a></li>
                                    <li className="current"> propos</li>
                                </ol>
                            </nav>
                        </div>
                    </div>

                    {/* About Section */}
                    <section id="about" className="about section">
                        <div className="container">
                            <div className="row position-relative">
                                <div className="col-lg-7 about-img" data-aos="zoom-out" data-aos-delay="200">
                                    <img src={bsl3} alt="About" />
                                </div>

                                <div className="col-lg-7" data-aos="fade-up" data-aos-delay="100">
                                    <h2 className="inner-title">"On a deux vies. La deuxime commence le jour o lon ralise quon nen a quune !"</h2>
                                    <h5 className="phil-title">"(Confucius, philosophe chinois 551-479 avant J.C.)"</h5>
                                    <div className="our-story">
                                        <h3>Mon Parcours</h3>
                                        <p>Professionnel multi-diplm, j'accompagne les individus depuis 8 ans dans divers domaines, notamment la reconversion professionnelle, le coaching de couple, l'orientation scolaire des enfants et la gestion du stress. J'intgre des approches psychologiques et existentielles pour fournir un soutien personnalis, identifier les besoins de formation et aider  lever les freins  la russite. Mon objectif est d'aider chacun  surmonter les obstacles et  raliser ses aspirations, en garantissant une transition russie dans leur parcours personnel et professionnel.</p>
                                        <ul>
                                            <li><i className="bi bi-check-circle"></i> <span>Une coute attentive pour comprendre vos besoins profonds et vos proccupations</span></li>
                                            <li><i className="bi bi-check-circle"></i> <span>Je vous apporte un soutien moral et motionnel, essentiel pour traverser les dfis de la vie.</span></li>
                                            <li><i className="bi bi-check-circle"></i> <span>Je vous aide  clarifier et  personnaliser vos aspirations, afin de transformer vos rves en ralits.</span></li>
                                            <li><i className="bi bi-check-circle"></i> <span>Je vous aide  dvelopper votre capacit  apprendre et  progresser</span></li>
                                        </ul>
                                        <p>"Je m'engage  personnaliser mon accompagnement en fonction de votre parcours unique et de vos objectifs spcifiques."</p>

                                        <div className="watch-video d-flex align-items-center position-relative">
                                            <i className="bi bi-play-circle"></i>
                                            <a href="https://www.youtube.com/" className="glightbox stretched-link">Watch Video</a>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                    <TeamSimpl />
                    <TestimonialsSimpl />
                </>
            )}
        </main>
    );
};

export default About;
